import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../styles/themes/vad/constants';
import { EVENT_TYPE } from '../../../../constants/index';

const useStyles = makeStyles(theme => ({
  root: {
    ...theme.shape.componentSpacing,
    [theme.breakpoints.up('lg')]: {
      marginBottom: theme.spacing(6), //this is to compensate the spacing created by Highlights section
    },
    '& h4': {
      marginBottom: theme.spacing(3),
      [theme.breakpoints.up('lg')]: {
        ...theme.mixins.marginLeft(pxToRem(-16)),
      },
    },
    '& svg': {
      width: theme.spacing(4),
      height: theme.spacing(4),
      ...theme.mixins.paddingRight(0),
    },
  },
  richTextOverride: {
    color: theme.palette.text.darkGrey,
    marginBottom: theme.spacing(2),
    '&.richText p': {
      ...theme.typography.body1,
      marginTop: '0',
      marginBottom: theme.spacing(2),
    },
  },
  highLightHeading: {
    marginTop: theme.spacing(4),
    [theme.breakpoints.up('lg')]: {
      marginTop: theme.spacing(6),
    },

    '& .heading-text': {
      marginTop: theme.spacing(5),
      marginLeft: 0,
      marginRight: 0,
    },
  },
  categoryTags: {
    display: 'flex',
    justifyContent: 'flex-start',
    flexWrap: 'wrap',
    '& span': {
      background: ({ EVENTS_TAG_THEME_COLOR, contentType }) =>
        contentType === EVENT_TYPE && EVENTS_TAG_THEME_COLOR,
      color: ({ contentType }) =>
        contentType === EVENT_TYPE && theme.mixins.White(),
      borderRadius: ({ contentType }) =>
        contentType === EVENT_TYPE && pxToRem(3),
    },
    '& .MuiChip-root': {
      ...theme.mixins.marginRight(pxToRem(10)),
      [theme.breakpoints.up('lg')]: {
        ...theme.mixins.marginRight(pxToRem(20)),
      },
    },
  },
  iconWrapper: {
    marginTop: theme.spacing(5),
    ...theme.mixins.paddingRight(theme.spacing(3)),
    '& .MuiAvatar-root': {
      marginRight: theme.spacing(2),
    },

    [theme.breakpoints.down('md')]: {
      transform: 'none',
      marginTop: theme.spacing(2),
    },
  },
  iconText: {
    display: 'flex',
    flexDirection: 'column',
  },
  tagContainer: {
    overflow: 'hidden',
    maxHeight: theme.spacing(10),
    transition: 'max-height 0.4s ease-in-out',
    [theme.breakpoints.up('lg')]: {
      maxHeight: '40px',
    },
    '&.showContent': {
      maxHeight: theme.spacing(50),
    },
  },
  toggleIcon: {
    ...theme.typography.body2Bold,
    position: 'relative',
    color: theme.mixins.AbuDhabiRed(),
    cursor: 'pointer',
    display: 'inline-block',
    marginTop: pxToRem(28),

    [theme.breakpoints.up('lg')]: {
      marginTop: pxToRem(34),
    },

    '&:after': {
      content: "''",
      position: 'absolute',
      border: `solid ${theme.mixins.AbuDhabiRed()}`,
      width: pxToRem(10),
      height: pxToRem(10),
      borderWidth: '0 2px 2px 0',
      transform: 'rotate(45deg)',
      top: `calc(50% - ${pxToRem(7)})`,
      ...theme.mixins.right(pxToRem(-24)),
    },
    '&.less': {
      '&:after': {
        top: `calc(50% - ${pxToRem(1.5)})`,
        transform: 'rotate(227deg)',
      },
    },
  },
}));

export default useStyles;
