import React from 'react';
import PropTypes from 'prop-types';
import Chip from '@material-ui/core/Chip';
import { Text } from '@sitecore-jss/sitecore-jss-react';
import useStyles from './CategoryTag.style';
import Typography from '../Typography';
import { getDictionaryText } from '../../../utils/getDictionaryText';
import { TAG_TEXT } from '../../../../constants/index';

const CategoryTag = props => {
  const { className, text, isClickable, isAuthorable, language, attributeFilter, analyticsData } = props;
  const { dataId, dataLink } = analyticsData || '';
  let { link } = props;
  if (!link || link === '#') {
    link = ''
  }
  const classes = useStyles();

  const buildAttributeListingPageUrl = () => {
    const attributeListingPageUrl = getDictionaryText('attributeListingPageUrl');
    let filteredLink;
    if (attributeFilter) {
      filteredLink = `/${language}${attributeListingPageUrl}?${TAG_TEXT}=${attributeFilter}`;
    }
    return filteredLink;
  }

  const tagField = {
    value: text,
  };

  if (isAuthorable) {
    return (
      <div className={`${classes.root} ${className} ${classes.chipRoot}`}>
        <span className={classes.chipLabel}>
          <Typography variant="labelBlack" component="span">
            <Text field={tagField} />
          </Typography>
        </span>
      </div>
    );
  }

  return (
    <React.Fragment>
      {isClickable ? (
        <Chip
          data-id={dataId}
          data-link={dataLink}
          label={
            <Typography variant="labelBlack" component="span">
              {text}
            </Typography>
          }
          component="a"
          href={link || buildAttributeListingPageUrl()} //if link is explictly mentioned while calling category tag then it will redirect to the same url, else attribute lising
          classes={{
            root: `${classes.root} ${className}`,
          }}
          clickable
        />
      ) : (
          <Chip
            component="div"
            label={
              <Typography variant="labelBlack" component="span">
                {text}
              </Typography>
            }
            classes={{
              root: `${classes.root} ${className}`,
            }}
          />
        )}
    </React.Fragment>
  );
};

CategoryTag.propTypes = {
  text: PropTypes.string.isRequired,
  className: PropTypes.string,
  link: PropTypes.string,
  isAuthorable: PropTypes.bool,
};

CategoryTag.defaultProps = {
  className: '',
  link: '#',
  isAuthorable: false,
  isClickable: false,
};

export default CategoryTag;
